import mutations from './mutations'
import actions from './actions'
import getters from './getters'

const state = {
    /**
     * sheet
     * @type {Array}
     */
    list: [],
    // 
    //  mousemove bodymouseup
    // 
    // 
    events: [],
    // 
    // 
    loaded: {
        //  1 
        // reload
        // alias
        // BUG 
        cols: [],
        rows: [],
        colMap: new Map(),
        rowMap: new Map()
    },
    // sheet
    scroll: {
        top: 0,
        left: 0
    },
    el: {
        offsetWidth: 0,
        offsetHeight: 0
    },
    // 
    max: {
        rowAlias: null,
        colAlias: null,
        rowPixel: 0,
        colPixel: 0
    },
    frozen: {
        col: [],
        row: [],
        // 
        alias: {
            row: null,
            col: null
        }
    },
    prompt: {
        show: false,
        texts: '',
        type: ''
    },
    popup: {
        show: false,
        title: '',
        type: ''
    }
}

export default {
    state,
    actions,
    mutations,
    getters
}


