<template>
<div class="row-head-line"
     :style="{ top, height }"></div>
</template>
<script>
import {
    unit
} from '../filters/unit'
export default {
    props: ['row', 'offsetTop'],
    computed: {
        top() {
            return unit(this.row.physicsBox.top - this.offsetTop)
        },
        height() {
            // 
            //  -1
            // 
            //  -1
            // 
            //  -2
            let rowHeight = this.row.physicsBox.height - 2 > 0 ? this.row.physicsBox.height - 2 : 0
            return unit(rowHeight)
        }
    }
}
</script>


